import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Box, Typography, TextField, Button, styled } from "@mui/material";
import axios from "axios";
import { useDispatch } from "react-redux";
import { authActions } from "../redux/store";
import toast from "react-hot-toast";

const Image = styled("img")({
  width: 150,
  margin: "auto",
  display: "flex",
  padding: "50px 0 0",
});

const Login = () => {
  const imageURL =
    "https://th.bing.com/th/id/R.88afbd2627f6bf4b99ae9efdaae6be87?rik=BZcsFbDn8gaelg&riu=http%3a%2f%2fcdn.dragosroua.com%2fwp-content%2fuploads%2f2014%2f11%2fblog1.jpg&ehk=fywN6YxaEPE5fe7ElTE%2fWDa%2bXEpGqbn6Q466QwJeuzw%3d&risl=&pid=ImgRaw&r=0";
  const navigate = useNavigate();
  const dispatch = useDispatch();

  //state
  const [inputs, setInputs] = useState({
    email: "",
    password: "",
  });

  //handle input change
  const handleChange = (e) => {
    setInputs((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  //form handle
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/v1/user/login", {
        email: inputs.email,
        password: inputs.password,
      });
      if (data.success) {
        localStorage.setItem("userId", data?.user._id);
        dispatch(authActions.login());
        toast.success("User login Successfully");
        navigate("/");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <form onSubmit={handleSubmit}>
        <Box
          maxWidth={450}
          display="flex"
          flexDirection={"column"}
          alignItems={"center"}
          justifyContent={"center"}
          margin="auto"
          marginTop={8}
          boxShadow="5px 2px 5px 2px rgb(0 0 0/0.7)"
          padding={4}
          borderRadius={5}
          backgroundColor={"white"}
        >
          <Image src={imageURL} alt="login" />
          <Typography
            variant="h4"
            padding={3}
            textAlign="center"
            sx={{ textTransform: "uppercase" }}
            fontFamily={"cursive"}
            color="#241468"
          >
            Login
          </Typography>
          <TextField
            placeholder="Enter Email"
            value={inputs.email}
            onChange={handleChange}
            variant="standard"
            name="email"
            margin="normal"
            type={"email"}
            required
          />
          <TextField
            placeholder="Enter Password"
            value={inputs.password}
            onChange={handleChange}
            variant="standard"
            name="password"
            margin="normal"
            type={"password"}
            required
          />
          <Button
            type="submit"
            sx={{ borderRadius: 2, marginTop: 3 }}
            variant="contained"
            color="primary"
          >
            Submit
          </Button>
          <Button
            onClick={() => navigate("/register")}
            sx={{ borderRadius: 2, marginTop: 3, textTransform: "none" }}
            color="primary"
          >
            Not a User ? Please Register
          </Button>
        </Box>
      </form>
    </>
  );
};

export default Login;
